<template>
  <div class="page" :id="id" v-bind:style="{height: height}">
    <div class="container">
      <h1>{{ title }}</h1>
      <transition-group name="showcaseitem" class="items-container">
        <ShowcaseItem 
          v-for="item in items_instance"
          :key="item.name"
          :item="item"
          :id="item.name"
          :imageSrc="item.image" />
      </transition-group>
    </div>
  </div>
</template>

<script>
import ShowcaseItem from './ShowcaseItem'

export default {
  name: "Showcase",
  props: {
    title: String,
    id: String,
    items: Array,
    height: String
  },
  components: {
    "ShowcaseItem": ShowcaseItem
  },
  data() {
    return {
      items_instance: [],
      expanded: false,
      selectedItem: null
    }
  },
  methods: {
    /*
    GetImageURL(path) {
      // depracated function. now using imgur for images
      // instead of storing locally
      return require("@/assets/imgs/" + path);
    },*/
    ShowItems(expansionRate) {
      if(this.expanded)
        return;

      var i = 0;
      var expansionInterval = setInterval(() => {
        this.items_instance.push(this.items[i]);

        if(i >= this.items.length - 1) {
          clearInterval(expansionInterval);
          this.expanded = true;
        }
        i++;
    }, expansionRate);
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.page {
  width: 100vw;
  padding-left: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 50%;
  height: 80%;
}

.items-container {
  padding: 16px;
  width: 100%;
  height: 80%;
}

.showcaseitem-enter-active, .showcaseitem-leave-active {
   transition: opacity 1s;
}

.showcaseitem-enter, .showcaseitem-leave-to {
   opacity: 0;
}
</style>
