<template>
   <div class="item" v-on:click="SelectItem()">
      <img :src="imageSrc" v-bind:alt="item.image" />
      <div class="label">
         <h3>{{ item.name }}</h3>
         <p class="tagline">{{ item.tagline }}</p>
      </div>
      <div class="awards hidden" :id="item.name+'-awards'">
         <div v-for="award in item.awards" :key="award.name" class="award">
            <svg class="award-icon" v-bind:style="{fill: award.color}" xmlns="http://www.w3.org/2000/svg" width="17" height="20" viewBox="0 0 24 24" fill="none" stroke="#ffffff" stroke-width="1.75" stroke-linecap="round" stroke-linejoin="round"><polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon></svg>
            <p>{{award.award}}</p>
         </div>
      </div>
      <div class="links hidden" :id="item.name+'-links'">
         <div v-for="link in item.links" :key="link.link" class="link">
            <a :href="link.link" target="_blank">
               <img :src="link.icon"/>
            </a>
         </div>
      </div>
   </div>
</template>

<script>
export default {
   name: "Showcase-Item",
   props: {
      item: Object,
      imageSrc: String
   },
   data() {
      return {
         hover: false
      }
   },
   methods: {
      SelectItem() {
         document.getElementsByClassName("selected-showcase-item").forEach(element => {
            element.classList.remove("selected-showcase-item");
         });
         document.getElementsByClassName("awards").forEach(element => {
            element.classList.add("hidden");
         })
         document.getElementsByClassName("links").forEach(element => {
            element.classList.add("hidden");
         })

         document.getElementById(this.item.name).classList.add("selected-showcase-item");

         if(document.getElementById(`${this.item.name}-awards`).childElementCount != 0)
            document.getElementById(`${this.item.name}-awards`).classList.remove("hidden");
         document.getElementById(`${this.item.name}-links`).classList.remove("hidden");
      }
   }
}
</script>

<style scoped>
.item {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-basis: 100%;

   transition: 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
   width: 100%;
   height: 12vh;
   margin: 6px 0 6px 0;

   backface-visibility: hidden;
   -webkit-backface-visibility: hidden;
   -moz-backface-visibility: hidden;
   transform: translate3d(0, 0, 0);
   -webkit-transform: translate3d(0, 0, 0);
   -moz-transform: translate3d(0, 0, 0)
}

.item:hover {
   cursor: pointer;
}

.item:hover .label .tagline, .selected-showcase-item .label .tagline {
   opacity: 1;
   height: 2vh;
}

.item:hover img {
   filter: saturate(100%)
}

.item img {
   height: 100%;
   width: 100%;
   object-fit: cover;
   border-radius: 10px;
   filter: saturate(50%);
   transition: 300ms;
}

.label {
   position: fixed;
   text-align: center;
   background-color: rgba(36, 36, 36, 0.80);
   padding: 6px 10px;
   border-radius: 8px;
   color: white;
   width: auto;
   display: inline-block;
   transition: 300ms;
}

.label h3 {
   font-size: 2vh;
   font-weight: 500;
}

.label p {
   font-size: 1.6vh;
   transition: 300ms;
   height: 0px;
}

.selected-item {
  flex-basis: 2;
}

.tagline {
   font-size: 12px;
   color: lightgray;
   opacity: 0;
   transition: 300ms;
}

.selected-showcase-item {
   height: 25vw;
   cursor: auto !important;
}

.selected-showcase-item img {
   filter: saturate(100%)
}

.hidden {
   opacity: 0;
   pointer-events: none;  
}

.awards {
   position: fixed;
   top: 3%;
   left: 2%;
   padding: 6px;
   transition: 300ms;
   background-color: rgba(36, 36, 36, 0.60);
   border-radius: 10px;
}

.award {
   display: flex;
   flex-direction: row;
   transition: 300ms;
}

.award p {
   color: white;
   font-size: 16px;
}

.award-icon {
   padding-right: 3px;
}

.links {
   position: fixed;
   bottom: 3%;
   display: flex;
   flex-direction: row;
   align-items: center;
   transition: 300ms;
   background-color: rgba(36, 36, 36, 0.60);
   border-radius: 10px;
   padding: 3px 3px 0 3px;
}

.link {
   width: 32px;
   height: auto;
   padding: 3px 3px 0 3px;
   transition: 100ms;
}

.link:hover {
   opacity: 0.5;
   cursor: pointer;
}
</style>
