<template>
   <div class="expandable">
      <li class="expandable-category-title">{{ title }}</li>
      <svg
      :id="title"
      class="arrow"
      v-on:click="ToggleItems"
      xmlns="http://www.w3.org/2000/svg"
      width="13"
      height="20"
      viewBox="0 0 24 16">
         <path d="M8.122 24l-4.122-4 8-8-8-8 4.122-4 11.878 12z"/>
      </svg>
      <transition-group name="item" tag="p" class="expanded-container">
         <p v-for="item in items_instance" v-bind:key="item" class="expanded-item">{{ item }}</p>
      </transition-group>
   </div>
</template>

<script>
export default {
   name: "Expandable",
   props: {
      title: String,
      items: Array,
      rate: Number,
      color: String
  },
  data() {
     return {
        items_instance: [],
        expanded: false,
        isMoving: false
     }
  },
  created() {
      // this is actually the arrow svg that goes by the id {{title}}
      this.$nextTick(() => {
         document.getElementById(this.title).style.fill = this.color;
      })
  },
  methods: {
     ToggleItems(e) {

         if(this.isMoving)
            return;

         var rate = this.rate;
         var target = e.target;

         // user has a chance to select the <path> element instead.
         // this makes sure only the parent <svg> element is used
         // in the contract/expand functions
         if(target.localName == "path") {
            target = e.target.parentNode;
         }

         this.isMoving = true;
         if(this.expanded) {
            this.ContractItems(rate, target);
         } else {
            this.ExpandItems(rate, target);
         }
     },
     ExpandItems(expansionRate, target) {
         if(this.expanded)
            return;

         target.classList.add("flipped-symbol");

         var i = 0;
         var expansionInterval = setInterval(() => {
            this.items_instance.push(this.items[i]);

            if(i == this.items.length - 1) {
               clearInterval(expansionInterval);
               this.expanded = true;
               this.isMoving = false;
            } else {
               this.items_instance[i] += ",";
            }
            i++;
        }, expansionRate);
     },
     ContractItems(contractionRate, target) {
         if(!this.expanded)
            return;

         target.classList.remove("flipped-symbol");
         
         var i = this.items_instance.length;
         var contractionInterval = setInterval(() => {
            this.items_instance.pop();
            if(i == 1) {
               clearInterval(contractionInterval);
               this.items_instance = [];
               this.expanded = false;
               this.isMoving = false;
            }
            i--;
         }, contractionRate);
     }
  }
};
</script>

<style scoped>
.expandable {
   display: flex;
   flex-direction: row;
   padding-top: 7px;
}

.expandable-category-title {
   width: 260px;
   font-weight: 600;
   list-style-type: disc;
   margin-left: 30px;
}

.expanded-container {
   display: grid;
   grid-auto-flow: column;
}

.expanded-item {
   padding-right: 10px;
   color: rgb(195, 195, 195);
}

.arrow {
   padding: 0 21px 0 21px;
   transition: 200ms;
}

.arrow:hover {
   opacity: 0.5;
   cursor: pointer;
}

.arrow path {
   user-select: none;
}

.item-enter-active, .item-leave-active {
   transition: opacity 1s;
}

.item-enter, .item-leave-to {
   opacity: 0;
}

.flipped-symbol {
   transform: scaleX(-100%);
}
</style>