<style>

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}
#app {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fcfcfc;
}

.page {
  padding-left: 25vw;
  width: 75vw;
  height: 100vh;
  display: flex;
  align-items: center;
  opacity: 0;
}

.nav {
  width: 128px;
  position: fixed;
  top: 50%;
  right: 7%;
  transform: translate(0, -50%);
  display: grid;
  text-align: right;
  z-index: 373;
}

.title {
  position: fixed;
  text-align: right;
  right: 7%;
  top: 7%;
}

.nav-element {
  text-decoration: none;
  color: #c3c3c3;
  padding: 12px 0px 12px 0px;
  -moz-user-select: none;
  transition: 200ms;
  font-size: 17px;
}

.nav-element:hover {
  text-decoration: underline;
  cursor: pointer;
}

.selected {
  padding-right: 17px;
  color: #5a5a5a;
}

h1 {
  font-size: 40px;
  color: rgb(39, 43, 39);
}

h2 {
  font-weight: bold;
}

p {
  color: rgb(75, 75, 75);
  font-size: 21px;
  font-weight: normal;
  line-height: 23px;
}

.visible {
  transition: 600ms opacity;
  opacity: 1;
}

.container {
  width: 35vw;
}
</style>
